<template>
    <div>
        <b-table striped hover :items="groups" :fields="fields"></b-table>
    </div>
</template>

<script>
    export default {
        name: "ScoreBoard",
        components: {},
        props: {
            id: Number,
        },
        data() {
            return {
        // Note `isActive` is left out and will not appear in the rendered table
                fields: ['name', 'score'],
                groups: Array,
            }
        },
        mounted() {
            this.fetchData()
        },
        methods: {
            async fetchData() {
                const res = await fetch("groups.json");
                const val = await res.json();
                console.log(val);

                this.groups = val;
            },
        },
    };
</script>

